import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController, ModalController, LoadingController, ToastController, ActionSheetController, AlertController } from 'ionic-angular';

import { IonicStorageProvider } from '../../providers/base-settings/storage';
import { ElectionProvider } from '../../providers/election/election';

@IonicPage()
@Component({
  selector: 'page-elections-list',
  templateUrl: 'elections.html'
})
export class ElectionsPage {
  // public electionsList: Array<{ id: string, name: string, name_full: string, candidates: number, status: string }>;
  public electionsList: any;
  public default_image: string;
  public query: string;
  public next_offset: string;
  private sessionUser: any;
  private sessionToken: string = '';
  private searchName: string;
  public searchFilterBasic: any;
  private isSearched: boolean = false;
  private selected_lead = 0;

  /**
   * Constructor
   */
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public menuCtrl: MenuController, public modalCtrl: ModalController,
    private loadingCtrl: LoadingController, private toastCtrl: ToastController,
    private ionStorage: IonicStorageProvider, private actionSheetCtrl: ActionSheetController,
    private alertCtrl: AlertController, private electionProv: ElectionProvider) {
    let env = this;

    // Initialize Targeted Lists Arr
    env.electionsList = [];

    // Set Defaults
    env.default_image = 'assets/elections/elections.png';
    env.query = "";

    // Check User Session
    // env.checkUserSession();

    // Temporary Data Collector
    env.getElections();
  }

  /**
   * When control enters Ionic View
   */
  ionViewDidEnter() {
    let env = this;
    // Enabling the menu
    env.menuCtrl.enable(true, 'mainMenu');
  }

  /**
   * Check User Session
   */
  checkUserSession() {
    let env = this;

    this.ionStorage.getItems('sessionuser').then(data => {
      if(data && data.token) {
        env.sessionUser = data;
        env.sessionToken = data.token;

        // Clear and Initialize Search Filter and Get Electios
        env.clearSearchFilter();
        this.searchByModalFilters(this.searchFilterBasic.name, this.searchFilterBasic.phone_mobile, this.searchFilterBasic.cities, this.searchFilterBasic.expected_sales, this.searchFilterBasic.sources, this.searchFilterBasic.statuses);
      } else {
        this.logout();
      }
    });
  }

  /**
   * Initialize Search Filter
   */
  clearSearchFilter() {
    this.searchFilterBasic = {};
    this.searchFilterBasic.name = '';
    this.searchFilterBasic.phone_mobile = '';
    this.searchFilterBasic.cities = [];
    this.searchFilterBasic.sources = [];
    this.searchFilterBasic.expected_sales = '';
    this.searchFilterBasic.statuses = [];
    this.searchName = '';
    this.isSearched = false;
  }

  /**
   * Manage Infinite Scroll
   */
  doInfinite(infiniteScroll: any) {
    if (this.electionsList.length >= 20) {
      // If next offset is not false, load the next list
      if (this.next_offset) {
        this.filterAndSearchElections(this.next_offset, false, false, infiniteScroll);
      }

      if(this.next_offset == '0') {
        infiniteScroll.complete();
      }
    } else {
      infiniteScroll.complete();
    }
  }

  /**
   * Force Reload this page data
   */
  forceReload(refresher) {
    let env = this;
    env.filterAndSearchElections('0', true, true);
    refresher.complete();
  }

  /**
   * Search Filter for Name
   */
	searchElectiosByName(ev: any, type: string) {
		let searchterm = ev.target.value;
    this.searchFilterBasic.name = searchterm;
    this.filterAndSearchElections('0', true, true);
	}

  /**
   * Search By Modal Filters
   */
  searchByModalFilters(name: string, phone_mobile: string, cities: string[], expected_sales: string, sources: string[], statuses: string[]) {
    // Clear Search Filter
    this.clearSearchFilter();

    // Name filter
    if(name != '') {
      this.searchFilterBasic.name = name;
      this.searchName = name;
      this.isSearched = true;
    }

    // Phone Number filter
    if(phone_mobile != '') {
      this.searchFilterBasic.phone_mobile = phone_mobile;
      this.isSearched = true;
    }

    // Cities filter
    if(cities.length > 0) {
      this.searchFilterBasic.cities = cities;
      this.isSearched = true;
    }

    // Expected Sales filter
    if(expected_sales != '') {
      this.searchFilterBasic.expected_sales = expected_sales;
      this.isSearched = true;
    }

    // Sources filter
    if(sources.length > 0) {
      this.searchFilterBasic.sources = sources;
      this.isSearched = true;
    }

    // Statuses filter
    if(statuses.length > 0) {
      this.searchFilterBasic.statuses = statuses;
      this.isSearched = true;
    }

    // Call Filter Method
    this.filterAndSearchElections('0', true, true);
  }

  /**
   * Formulate a Search Query to Filter Electios
   */
  filterAndSearchElections(offset?: string, refresh?: boolean, showLoader?: boolean, infiniteScroll?: any) {
    let query = "";
    let queryImploderArr = ['name', 'phone', 'cities', 'sources', 'expectedsales', 'statuses'];

    // Electio Name
    queryImploderArr['name'] = '';
    if(typeof(this.searchFilterBasic.name) != 'undefined' && this.searchFilterBasic.name != '') {
      queryImploderArr['name'] = "(CONCAT(elections.first_name, ' ', elections.last_name) LIKE '%"+ this.searchFilterBasic.name +"%')";
    }

    // Electio Mobile Phone Number
    queryImploderArr['phone'] = '';
    if(typeof(this.searchFilterBasic.phone_mobile) != 'undefined' && this.searchFilterBasic.phone_mobile != '' && this.searchFilterBasic.phone_mobile.length == 10) {
      queryImploderArr['phone'] = "(elections.phone_mobile LIKE '"+ this.searchFilterBasic.phone_mobile +"%')";
    }

    // Electio Cities
    queryImploderArr['cities'] = '';
    if(this.searchFilterBasic.cities.length > 0) {
      let query_city_arr = [];
      for(let city in this.searchFilterBasic.cities) {
        let temp = "elections.primary_address_city = '"+ this.searchFilterBasic.cities[city] +"'";
        query_city_arr.push(temp);
      }

      queryImploderArr['cities'] = "("+ query_city_arr.join('OR ') +")";
    }

    // Electio Sources
    queryImploderArr['sources'] = '';
    if(this.searchFilterBasic.sources.length > 0) {
      let query_source_arr = [];
      for(let source in this.searchFilterBasic.sources) {
        let temp = "elections.lead_source = '"+ this.searchFilterBasic.sources[source] +"'";
        query_source_arr.push(temp);
      }

      queryImploderArr['sources'] = "("+ query_source_arr.join('OR ') +")";
    }

    // Expected Sales
    queryImploderArr['expectedsales'] = '';
    if(typeof(this.searchFilterBasic.expected_sales) !='undefined' && this.searchFilterBasic.expected_sales != '') {
      queryImploderArr['expectedsales'] = "(elections_cstm.expected_sales_c = '"+ this.searchFilterBasic.expected_sales +"')";
    }

    // Statuses
    queryImploderArr['statuses'] = '';
    if(this.searchFilterBasic.statuses.length > 0) {
      let query_status_arr = [];
      for(let status in this.searchFilterBasic.statuses) {
        let temp = "elections.status = '"+ this.searchFilterBasic.statuses[status] +"'";
        query_status_arr.push(temp);
      }

      queryImploderArr['statuses'] = "("+ query_status_arr.join('OR ') +")";
    }

    /**
     * Set Params
     */

    // Query Params
    queryImploderArr.forEach(item => {
      query = (queryImploderArr[item] != '')
        ? (query != '')
            ? "("+ query +" AND " + queryImploderArr[item] +")"
            : queryImploderArr[item]
        : query;
    });

    // Set Offset
    offset = (offset) ? offset : '0';
    // Set Refresh
    refresh = (refresh) ? refresh : false;
    // Set Loader
    showLoader = (showLoader) ? showLoader : false;
    // Set Infinite Scroll
    infiniteScroll = (infiniteScroll) ? infiniteScroll : false;

    // Call Method to Retrieve elections list
    this.getElectionsList(query, offset, refresh, showLoader, infiniteScroll);
	}

  /**
   * Get Electios Listing
   */
  getElectionsList(query: string, offset: string, refresh: boolean, showLoader: boolean, infiniteScroll?: any) {
    let env = this;

    // First Loader Start
    let loader;
    if(showLoader) {
      loader = this.loadingCtrl.create({ content: "Loading...", enableBackdropDismiss: true });
      loader.present();
    }

    // Set default query for hide closed and won elections from listing by default
    query = (query == "") ? "(elections.status != 'closed_lost' AND elections.status != 'closed_won')" : query;
    query = (query != '') ? query + " AND " : "";
    query = query + "(elections.assigned_user_id = '"+ env.sessionUser.id +"')";

    // Fetch Params
    let params = {
      'offset': offset,
      'query': query,
      'order_by': 'date_entered DESC',
      'token': env.sessionToken
    };

    let refreshParams = (refresh) ? true: false;
  }

  /**
   * Get Elctions Listing
   * This is a temporary method to fetch and show data
   */
  getElections() {
    let env = this;
    env.electionsList = env.electionProv.getElections();
  }

  /**
   * Go to Electio Details Page
   */
  gotoElectionDetails(election_id: string) {
    this.navCtrl.push('ElectionDetailsPage', { election_id: election_id });
  }

  /**
   * Logout
   */
  logout() {
    this.ionStorage.removeItem('sessionuser');
    this.navCtrl.setRoot('LoginPage');
  }
}



