import { PipesModule } from '../../pipes/pipes.module';

import { ElectionsPage } from './elections';
import { ElectionProvider } from '../../providers/election/election';

@NgModule({
  declarations: [
    ElectionsPage,
  ],
  imports: [
    IonicPageModule.forChild(ElectionsPage),
    CacheModule.forRoot(),
    PipesModule
  ],
  providers: [
    ElectionProvider
  ]
})
export class ElectionsPageModule {}



